<template>
  <div class="intro-wrapper">
    <div class="intro-background"></div>
    <div class="intro">
      <h2>
        Coding is
        <span class="dynamic-title">{{typedValue}}</span>
        <span
          class="cursor"
          v-bind:class="{'typing': typingStatus}"
          v-html="cursorSpace"
        />
      </h2>

      <div class="intro-icons">
        <p class="intro-find-me">
          Find me on:
        </p>
        <v-icon @click="githubIconClicked">fa-github</v-icon>
        <v-icon @click="linkedinIconClicked">fa-linkedin</v-icon>
      </div>

      <p>
      Scroll down for more details.
      </p>
      <span class="scrolldown-icon-wrapper">
      <v-icon medium>mdi-chevron-down</v-icon>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Introduction',
  data: () => ({
    cursorSpace: '&nbsp;',
    textArray: ['fun', 'awesome', 'life'],
    typingSpeed: 200,
    erasingSpeed: 100,
    newTextDelay: 2000,
    typingStatus: false,
    typedValue: '',
    textArrayIndex: 0,
    charIndex: 0,
  }),
  methods: {
    typeText() {
      if (this.charIndex < this.textArray[this.textArrayIndex].length) {
        if (!this.typingStatus) {
          this.typingStatus = true;
        }
        this.typedValue += this.textArray[this.textArrayIndex].charAt(this.charIndex);
        this.charIndex += 1;
        setTimeout(this.typeText, this.typingSpeed);
      } else {
        this.typingStatus = false;
        setTimeout(this.eraseText, this.newTextDelay);
      }
    },
    eraseText() {
      if (this.charIndex > 0) {
        if (!this.typingStatus) {
          this.typingStatus = true;
        }
        this.typedValue = this.textArray[this.textArrayIndex].substring(0, this.charIndex - 1);
        this.charIndex -= 1;
        setTimeout(this.eraseText, this.erasingSpeed);
      } else {
        this.typingStatus = false;
        this.textArrayIndex += 1;
        if (this.textArrayIndex >= this.textArray.length) {
          this.textArrayIndex = 0;
        }
        setTimeout(this.typeText, this.typingSpeed + 1000);
      }
    },
    githubIconClicked() {
      window.open('https://github.com/kyoronz', '_blank');
    },
    linkedinIconClicked() {
      window.open('https://my.linkedin.com/in/law-yong-hui-1a3a51183', '_blank');
    },
  },
  created() {
    setTimeout(this.typeText, this.newTextDelay + 200);
  },
};
</script>

