<template>
  <div class="lightbox" v-if="value" @click="hide">
    <div class="lightbox-close-icon" @click="hide">
      <v-icon @click="hide">mdi-close</v-icon>
    </div>
    <div><h2>{{ title }}</h2></div>
    <div>
      <v-icon>mdi-keyboard-arrow-left</v-icon>
    </div>
    <div class="lightbox-image">
      <img v-bind:src="image"/>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    value: Boolean,
    thumbnails: {
      type: String,
      required: false,
    },
    title: {
      type: String,
      required: false,
    },
    image: {
      type: String,
      required: true,
    },
    // visible: {
    //   type: Boolean,
    //   required: true,
    // },
  },
  data: () => ({
    index: 0,
  }),
  methods: {
    show() {
      this.$emit('input', true);
    },
    hide() {
      this.$emit('input', false);
    },
    hasPrev() {
      return this.index - 1 <= 0;
    },
    hasNext() {
      return this.index + 1 >= images.length;
    },
    next() {
      if (this.hasNext()) {
        this.index += 1;
      }
    },
    prev() {
      if (this.hasPrev()) {
        this.index -= 1;
      }
    },
  },
};
</script>

