<template>
  <div class="contact-form-wrapper">
    <v-container>
      <v-form class="contact-form" ref="form" lazy-validation>
        <h2 class="contact-form-title">Contact Me</h2>
        <v-text-field
          v-model="contact.email"
          v-bind:rules="rules.email"
          label="Email Address"
          outlined
          require>
        </v-text-field>
        <v-text-field
          v-model="contact.name"
          v-bind:rules="rules.name"
          label="Name"
          outlined
          require>
        </v-text-field>
        <v-text-field
          v-model="contact.subject"
          v-bind:rules="rules.subject"
          label="Subject"
          outlined
          require>
        </v-text-field>
        <v-textarea
          v-model="contact.message"
          v-bind:rules="rules.message"
          label="Message"
          outlined
          require>

        </v-textarea>
        <v-btn color="#000032" dark @click="validate">Submit</v-btn>
      </v-form>
    </v-container>

    <v-dialog persistent v-model="loading" width="200">
      <v-card class="contact-form-loading">
        <div class="contact-form-loading-icon">
          <v-progress-circular indeterminate color="primary"/>
        </div>
        <h4>Processing</h4>
      </v-card>
    </v-dialog>
    <v-dialog v-model="complete" width="200">
      <v-card class="contact-form-loading">
        <h4>{{errorMessage ? errorMessage : 'The message is sent.'}}</h4>
        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            text
            @click="complete = false"
          >
            Okay
          </v-btn>

          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
const axios = require('axios');

const source = axios.CancelToken.source();
const cancelable = (promise) => {
  let hasCancelled = false;

  return {
    promise: promise.then((v) => {
      if (hasCancelled) {
        const errorMessage = { isCancelled: true };
        throw errorMessage;
      }

      return v;
    }),
    cancel: () => { hasCancelled = true; },
  };
};

export default {
  data: () => ({
    loading: false,
    complete: false,
    errorMessage: '',
    url: 'https://kyoron-mail.herokuapp.com/api/portfolio/contact',
    // url: 'https://gmail.us19.list-manage.com/subscribe/post?u=4ef141adf1a5d3257e16353af&amp;id=eb75c15687',
    // url: 'https://formsubmit.co/ajax/lawyonghuicareer@gmail.com',
    // url: 'https://submit-form.com/ocociT7CQIgaF_vuYpdes',
    contact: {
      email: '',
      name: '',
      subject: '',
      message: '',
    },
    rules: {
      email: [
        (v) => !!v || 'Email is required',
        (v) => /.+@.+\..+/.test(v) || 'E-mail format is invalid',
      ],
      name: [
        (v) => !!v || 'Name is required',
      ],
      subject: [
        (v) => !!v || 'Subject is required',
      ],
      message: [
        (v) => !!v || 'Message is required',
      ],
    },
  }),
  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        // const params = new URLSearchParams();
        // params.append('email', this.contact.email);
        // params.append('name', this.contact.name);
        // params.append('subject', this.contact.subject);
        // params.append('message', this.contact.message);
        axios({
          method: 'post',
          url: this.url,
          data: {
            email: this.contact.email,
            name: this.contact.name,
            subject: this.contact.subject,
            message: this.contact.message,
          },
          // data: params,
          cancelToken: source.token,
        })
          .catch((error) => {
            this.loading = false;
          })
          .then((response) => {
            this.complete = true;
            this.errorMessage = '';
            this.$refs.form.reset();
            this.loading = false;
          }, (reject) => {
            this.complete = true;
            this.errorMessage = 'Fail to send message';
            this.loading = false;
          });
      }
    },
  },
  beforeDestroy: () => {
    source.cancel();
  },
};
</script>

